.header{
  background-color: #F55A5A;
  color: white;
  padding: 1.2em 2em;
  display: flex;
}

.icon{
  font-size: 2rem;
  padding-right: 0.7rem;
}

.logo{
  font-size: 1.2rem;
}

.card {
  display: flex;
  gap: 1em;
  margin: auto;
  padding-top: 4em;
  padding-bottom: 2em;
  max-height: 350px;
  max-width: 700px;
  border-bottom: 1px solid hsl(0, 0%, 81%)
}

.card:last-child{
  border-bottom: none;
}

.container {
  padding-top: 2em;
  width: 600px;
}

img{
  padding-right: 1.5em;
  width: 40%;
}

.location--dot{
  color: #F55A5A;
  padding-right: 0.5em;
}
.location{
  letter-spacing: 0.15em;
  padding-right: 1em;
  text-transform: uppercase;
}

a{
  color: #918E9B;
  font-size: 0.9rem;
}

h1{
  margin-top: 0.2em;
}

.endDate, .startDate{
  font-weight: bold;
}

@media(max-width:768px){
  .card{
    display: flex;
    flex-direction: column;
    margin: 2em;
    padding: 0;
    max-height: none;
    
  }
  img{
    width: 100%;

    
  }
  .container{
    padding: 0;
    width: 100%;
    padding-bottom: 0.8em;
  }
}